.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.region-item-text {
  pointer-events: none;
  text-anchor: middle;
}

.grad-legend-svg {
  margin-left: 5px;
  padding-left: 5px;
  overflow: overlay;
}
